import { ApiService } from 'src/app/Services/Api/Api.service';
import { Component, OnInit, Input } from '@angular/core';
import { StorageWebService } from 'src/app/Services/Storage/StorageWeb.service';
import { Socket } from 'ngx-socket-io';
import { ModalController } from '@ionic/angular';


@Component({
  selector: 'app-chat-modal',
  templateUrl: './chat-modal.page.html',
  styleUrls: ['./chat-modal.page.scss'],
})
export class ChatModalPage implements OnInit {
  
  @Input() type: any;
  users: any[] = [];
  usersSelect = {
    name: 'Mi sala de chat',
    users: []
  };

  customer: any = {
    name: '',
    login: '',
    clave: '',
    company: '',
    userID: ''
  };

  customers = [];
  constructor(
    private apiService: ApiService,
    private storage: StorageWebService,
    private socket: Socket,
    private modalCtrl: ModalController
  ) { }

  ngOnInit() {
  }

  async ionViewWillEnter() {
    if (this.type === 'chat') {
      this.usersSelect = {
        name: 'Mi sala de chat',
        users: []
      };
      const login = await this.storage.getLogin();
  
      if (login) {
        console.log(login[0]);

        this.apiService.getCustomersCompany(login[0].CompanyID).subscribe((data: any) => {

          console.log(data, 'DaTAMAN');
          if (data.status === 'ok') {

            if (data.response.length > 0) {
              data.response.forEach((item) => {
                item.ID = item.login;
                this.customers.push(item);
              })
            }
            this.customers = data.response;
          }
  
        });

        this.apiService.getUsersCompany({AccessToken: login[0].AccessToken}).subscribe((data: any) => {
  
          data = JSON.parse(data);
          console.log(data);
          this.users = data[0];
  
          this.users = this.users.filter((item) => {
            return item.ID !==  login[0].UserID;
          });
  
          this.usersSelect.users.push(login[0].UserID);


  
        });


       
      }
    }
    
  }

  selection( event ) {

    if (event.detail.checked) {
      this.usersSelect.users.push(event.detail.value);
    } else {
      this.usersSelect.users = this.usersSelect.users.filter((item) => item !== event.detail.value);
    }
  }

  createdChat() {
    this.socket.emit('chat', this.usersSelect);
    this.modalCtrl.dismiss({
      status: 'ok'
    });
  }

  async createdCustomer() {

    const login = await this.storage.getLogin();
  
    if (login) {
        console.log(login[0]);
        this.customer.company = login[0].CompanyID;
        this.customer.userID = login[0].UserID;
        this.apiService.createdCustomer(this.customer).subscribe((data: any) => {
          console.log(data, ' data mas');
          if (data.status === 'ok') {
            this.modalCtrl.dismiss({
              status: 'ok'
            });
          } else {
            this.modalCtrl.dismiss({
              status: 'error',
              error: data.error
            });
          }
        })
        
      }
    
  }

}
