import { Component } from '@angular/core';
import { Socket } from 'ngx-socket-io';
import { ToastController, AlertController, ModalController } from '@ionic/angular';
import { Observable, observable } from 'rxjs';
import { ApiService } from '../../Services/Api/Api.service';
import { DataComparnyService } from 'src/app/Services/dataCompany.service';
import { StorageWebService } from 'src/app/Services/Storage/StorageWeb.service';
import { ChatModalPage } from '../Modals/chat-modal/chat-modal.page';
import { Router } from '@angular/router';
import { ToastService } from 'src/app/Services/Utilities/Toast.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  mensaje: any;
  mensajes: any[] = [];
  nombreUsuario: any = '';

  chatUser: any = '';

  listChat = [];

  RoleCA = false;

  constructor(private socket: Socket,
              private toastCtrl: ToastService,
              private alertCtrl: AlertController,
              private dataCompany: DataComparnyService,
              private apiService: ApiService,
              private storage: StorageWebService,
              private modalCtrl: ModalController,
              private router: Router
              ) {
                this.newChat().subscribe(() => {
                  this.loadChats();
                })
              }

  async ionViewDidEnter() {
   // this.preventAlert().then(() => {
     this.loadChats();
   // });
  }

  async loadChats() {
    this.dataCompany.getVista('2');
    const myData = await this.storage.getLogin();
   
    if (myData) {
      this.RoleCA = myData[0].ROLE === 'CA' ? true : false;
      if (myData[0].type === 'exter') {
        this.apiService.getChats(myData[0].login).subscribe((data: any) => {
          this.listChat = data.response;
      });
      } else if ( myData[0].type === 'inter') {
        this.apiService.getChats(myData[0].UserID).subscribe((data: any) => {
          this.listChat = data.response;
      });
      }
      
    }
  }


  async createdChat() {
    
      const modal = await this.modalCtrl.create({
        component: ChatModalPage,
        componentProps: {
          type: 'chat'
        }
      });

      await modal.present();

      const close = await modal.onWillDismiss();

      if (close) {
        console.log('Michat', close);
        const myData = await this.storage.getLogin();
   
        if (myData) {
          setTimeout(() => {
            this.apiService.getChats(myData[0].UserID).subscribe((data: any) => {
              this.listChat = data.response;
              console.log(data, ' data');
              console.log(data.response);
          })
          }, 1000)
        }
        
      }
  }


  async createdCustomer() {
    
    const modal = await this.modalCtrl.create({
      component: ChatModalPage,
      componentProps: {
        type: 'customer'
      }
    });
    await modal.present();

    const customer = await modal.onWillDismiss();

    if (customer) {
      if (customer.data) {
        if (customer.data.status === 'ok') {
          this.toastCtrl.newCreatedToast('Cliente creado con Ã©xito', true)
        } else if (customer.data.status === 'error') {
          this.toastCtrl.newCreatedToast('No se pudo crear el cliente', true)
        }
      }
     
    }
}

  async saveChat( chat ) {
    const save = await this.storage.insertChat(chat);

    if (save) {
      this.router.navigate(['/messages']);
    }
  }

  newChat(): Observable<any> {
    const observer = new Observable((obs) => {
        this.socket.on('newchat', (data) => {
          obs.next(data);
        })
    })

    return observer;
  }
}
